.NevBar{
    position: fixed;
    width: 100%;
    height: 40px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #111;
}

.logo{
    position: fixed;
    left: 20px;
    width: 80px;
    
}
.avatar{
    position: fixed;
    right: 20px;
    width: 30px;
}
.sing{
    text-align:center;
}
*{
    padding: 0;
    margin: 0;
}
body{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #111;
}
.banner{
    
    background-size: cover;
    height:  651px;
    color:white;
   
}
.content{
    padding-top: 321px;
    height: 190px;
    padding-left:15px;
    
}
.title{
font-size: 3rem;
font-weight: 700;
padding-bottom: 0.3rem;
}
.button{
    color: white;
    border:none ;
    outline:none;
    font-weight:700;
    border-radius:5px;
    margin-left: 1em;
    padding-left:3rem;
    padding-right:2rem ;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color:rgba(51, 51, 51, 0.5) ;
    cursor: pointer;
}
.button:hover{
    color: black;
    background-color:#e6e6e6;
}
.discreption{
    width:45rem;
    line-height:1.3;
    padding-top:1rem;
    padding-left:1em;
    font-size:1em;
    height: 80px;
    max-width:350px;
    
}
.fade_bottom{
    height: 16.26rem;
  background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}
.row{
    margin-left:20px;
    color:#fff;
}
.posters{
  display: flex; 
  padding: 20px;
  overflow-x:scroll;
  overflow-y: hidden;
}
.posters::-webkit-scrollbar{
    display:none ;
}
.poster{
    max-height: 250px ;
    margin-right:10px ;
}
.sPoster{
    max-height:100px;
    margin-right:10px ;
}
